<template>
  <div class="mt-4">
    <loading :active.sync="isLoading"></loading>

    <h2></h2>

    <div class="text-right mt-sm-2 mt-4">
      <button class="btn btn-primary" @click="openCouponModal('new')">
        
      </button>
    </div>

    <table class="table table-admin table-rwd mt-3">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td data-th="">{{ item.title }}</td>
          <td data-th="">{{ item.percent }}</td>
          <td data-th="">{{ item.deadline.datetime }}</td>
          <td data-th="">
            <span v-if="item.enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td data-th="">
            <div class="btn-group">
              <button type="button" class="btn btn-outline-primary btn-sm"
              @click="openCouponModal('edit',item)"></button>
              <button type="button" class="btn btn-outline-danger btn-sm"
              @click="openCouponModal('delete',item)"></button>
            </div>
          </td>
        </tr>
      </tbody>

    </table>

    <pagination :pagedata="pagination" @update="getCoupons"></pagination>
    //  coupons
    <div id="couponModal" class="modal fade"
     tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          // header
          <div class="modal-header bg-dark text-white">
            <h5 id="exampleModalLabel" class="modal-title">
              <span v-if="tempCoupons.id"> {{ tempCoupons.title }}</span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          //body
          <div class="modal-body text-left">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <input id="couponTitle" v-model="tempCoupons.title"
                  type="text" class="form-control" placeholder="">
                </div>
                <div class="form-group">
                  <label for="coupon_code"></label>
                  <input
                    id="coupon_code"
                    v-model="tempCoupons.code"
                    type="text"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="title"></label>
                  <input id="couponPercent" v-model="tempCoupons.percent"
                  type="text" class="form-control" placeholder="">
                </div>
                <div class="form-group">
                  <label for="couponDeadline"></label>
                  <input
                    id="couponDeadline"
                    v-model="coupon_date"
                    type="date"
                    class="form-control">
                </div>
                <div class="form-group">
                  <label for="couponDeadtime"></label>
                  <input
                    id="couponDeadtime"
                    v-model="coupon_time"
                    type="time"
                    step="1"
                    class="form-control">
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input id="enabled" v-model="tempCoupons.enabled"
                      class="form-check-input" type="checkbox">
                    <label class="form-check-label" for="enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          // footer
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              
            </button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <div id="delCouponModal" class="modal fade"
      tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
              aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 id="exampleModalLabel" class="modal-title">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupons.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              
            </button>
            <button type="button" class="btn btn-danger" @click="deleteCoupon">
              
            </button>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
/* global $ */
import pagination from '@/components/Pagination.vue'
export default {
  components: {
    pagination
  },
  data () {
    return {
      coupons: [],
      pagination: {},
      tempCoupons: {
        title: '',
        code: '',
        percent: 0,
        enabled: false,
        deadline_at: 0
      },
      coupon_date: '', //  >> deadline_at
      coupon_time: '', //  >> deadline_at
      isLoading: false
    }
  },
  props: ['token'],
  created () {
    this.getCoupons()
  },
  methods: {
    getCoupons (num = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/coupons?page=${num}`
      this.axios.get(api).then((res) => {
        this.coupons = res.data.data
        this.pagination = res.data.meta.pagination // 
        this.isLoading = false
      })
      if (this.tempCoupons.id) {
        this.tempCoupons = {
          title: '',
          code: '',
          percent: 0,
          enabled: false,
          deadline_at: 0
        }
        this.coupon_date = ''
        this.coupon_time = ''
      } else {
        // 
        this.tempCoupons = {
          title: '',
          code: '',
          percent: 0,
          enabled: false,
          deadline_at: 0
        }
        this.coupon_date = ''
        this.coupon_time = ''
      }
    },
    openCouponModal (status, item) {
      if (status === 'new') {
        this.tempCoupons = {
          title: '',
          code: '',
          percent: 0,
          enabled: false,
          deadline_at: 0
        }
        $('#couponModal').modal('show')
      } else if (status === 'edit') {
        //  API """"
        this.isLoading = true
        const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/coupon/${item.id}`
        this.axios.get(url).then((res) => {
          this.tempCoupons = res.data.data;
          [this.coupon_date, this.coupon_time] = this.tempCoupons.deadline.datetime.split(' ')
          $('#couponModal').modal('show')
          this.isLoading = false
        }).catch(() => {
          this.isLoading = false
        })
      } else if (status === 'delete') {
        //  API """"
        this.isLoading = true
        const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}
        /admin/ec/coupon/${item.id}`
        this.axios.get(url).then((res) => {
          this.tempCoupons = res.data.data
          $('#delCouponModal').modal('show')
          this.isLoading = false
        })
      }
    },
    updateCoupon () {
      if (this.tempCoupons.id) {
        // 
        this.isLoading = true
        this.tempCoupons.deadline_at = `${this.coupon_date} ${this.coupon_time}`
        const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}
        /admin/ec/coupon/${this.tempCoupons.id}`
        this.axios.patch(url, this.tempCoupons)
          .then(() => {
            $('#couponModal').modal('hide')
            // this.$bus.$emit('notice-user', '')
            this.isLoading = false
            this.getCoupons()
          }).catch((error) => {
            this.isLoading = false
            const errorText = error.response.data.message
            console.log(errorText)
            $('#couponModal').modal('hide')
            // this.$bus.$emit('notice-user', `,+${errorText}`)
          })
      } else {
        //  Coupon
        this.tempCoupons.deadline_at = `${this.coupon_date} ${this.coupon_time}`
        this.isLoading = true
        const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/coupon`
        this.axios.post(url, this.tempCoupons)
          .then(() => {
            $('#couponModal').modal('hide')
            // this.$bus.$emit('notice-user', '')
            this.isLoading = false
            this.getCoupons()
          }).catch(() => {
            this.isLoading = false
            $('#couponModal').modal('hide')
            // this.$bus.$emit('notice-user', '')
          })
      }
    },
    deleteCoupon () {
      // 
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/admin/ec/coupon/${this.tempCoupons.id}`
      this.axios.delete(url).then(() => {
        $('#delCouponModal').modal('hide')
        // this.$bus.$emit('notice-user', '')
        this.isLoading = false
        this.getCoupons()
      }).catch(() => {
        this.isLoading = false
        $('#delCouponModal').modal('hide')
        // this.$bus.$emit('notice-user', '')
      })
    }
  }
}
</script>
